
import { useEffect, useState } from "react"; 
import classes from "./AvailableMeals.module.css";
import Card from "../UI/Card";
import MealItem from "./Mealitem/MealItem";



const AvailableMeals = () => {
  const [meals, setMeals] = useState([]);
  const [isLoading, setIsLoading] = useState(true)
  const [httpError, setHttpError] = useState(null);

  useEffect(() => {
    const fetchMeals = async () => {
        const response = await fetch('https://react-http-f00bb-default-rtdb.firebaseio.com/meals.json');
        
        if(!response.ok) throw new Error('Error fetching your request');

      const data = await response.json();

      const loadedMeals = [];

      for(const key in data){
        loadedMeals.push({
          id: key,
          name: data[key].name,
          description: data[key].description,
          price: data[key].price,
        })
      }
      setMeals(loadedMeals)
      setIsLoading(false);
    };

    fetchMeals().catch(error => {
      setIsLoading(false);
      setHttpError(error.message);
    });
  },[])


  if(isLoading) {
    return (
      <section className={classes['meals-loader']}>
          <p className={classes['text-align']}>Loading ...</p>
      </section>
      );
  }

  if(httpError) {
    return (
      <section className={classes['meals-error']}>
        <p>{httpError}</p>
      </section>
    )
  }
  const mealsList = meals.map((meal) => (
    <MealItem
      id={meal.id}
      key={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    />
  ));

  return (
    <section className={classes.meals}>
      <Card>
        {!isLoading && !httpError && <ul>{mealsList}</ul>}
      </Card>
    </section>
  );
};

export default AvailableMeals;
